import React from 'react';
import { Link } from 'react-router-dom';

import { buttonMenu } from './styles';

import logo from '../../../logo.svg'

function Navbar() {
  return (
    <nav className="navbar sticky-top navbar-expand-lg navbar-light bg-dark">
      <Link class="navbar-brand ml-5" to="/">
        <img src={logo} alt="logo" style={{ width:'35px' }}/>
      </Link>
      
      <buttonMenu className="navbar-toggler" style={{border: 'none'}} type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span>
          <i className="fas fa-bars" style={{ color: 'white'}}></i>
        </span>
      </buttonMenu>
    
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav m-auto">
          <li class="nav-item active">
            <Link className="nav-link text-white text-uppercase ml-5" to="/">
              Início&nbsp;
              <i class="fas fa-home"></i> 
              <span class="sr-only">
                (current)</span>
            </Link>
          </li>
          <li className="nav-item">
            <Link class="nav-link text-white text-uppercase ml-5" to="/noticias">
              Noticias
            </Link>
          </li>
          <li className="nav-item">
            <Link class="nav-link text-white text-uppercase ml-5" to="/contato">
              Contato
            </Link>
          </li>
          
        </ul>

      </div>
  </nav>
  )
}

export default Navbar;