import React, { Component } from 'react';

import { Link } from 'react-router-dom';

import { ComponentNotFound } from './styles';

class NotFoundPage extends Component {
  render() {
    return (
      <ComponentNotFound className="container">
        <div className="row">
          <div className="col-md-12">
            <div className="error-template">
              <h1>Ops!</h1>
              <h2>Error 404 Not Found</h2>
              <div className="error-details">
                Desculpe, ocorreu um erro. A página solicitada não foi encontrada
              </div>
              <div className="error-actions">
                <Link to="/" className="btn btn-outline-primary btn-lg">
                  <i className="fas fa-home" />&nbsp;Voltar ao Início
                </Link>

                <Link to="/contato" className="btn btn-outline-secondary btn-lg">
                  <i className="fas fa-envelope" />&nbsp;Ajuda
                </Link>
              </div>
            </div>
          </div>
        </div>
      </ComponentNotFound>
    )
  }
}

export default NotFoundPage;