import styled from 'styled-components';

const FooterContainer  = styled.footer`
  .footer-middle{
    background: var(--mainDark);
    padding-top: 3rem;
    color: var(--mainWhite);
  }

  .footer-bottom {
    padding-top: 3rem;
    padding-bottom: 2rem;
  }

  ul li a {
    color: var(--mainGrey);
    text-decoration: none;
  }

  ul li a:hover {
    color: var(--mainLightGrey);
  }
`;

export { FooterContainer };