import React, { Component } from 'react';

import { Link } from 'react-router-dom';

class Contato extends Component {
  render() {
    return (
      <section className="my-5 py-5">
        <div className="container">
          <div className="well well-sm">
            <h3>
              <strong> Our Location </strong>
            </h3>
          </div>

          <div className="row">
            <div className="col-md-7">
            <iframe src="https://www.google.com/maps/d/u/0/embed?mid=1jwgNG3bxtJFOLbC9Pla7S3QrtCy_PK0P" style={{ border: '0', width: '100%', height: '315px', frameborder: '0' }} allowFullScreen />
            </div>
          
          <div className="col-md-5">
            <h4>
              <strong>
                Contato
              </strong>
            </h4>

              <form>
                <div className="form-group">
                  <input type="text" className="form-control" placeholder="Nome"/>
                </div>

                <div className="form-group">
                  <input type="text" className="form-control" placeholder="Email"/>
                </div>

                <div className="form-group">
                  <input type="tel" className="form-control" placeholder="Telefone"/>
                </div>

                <textarea className="form-control" cols="30" rows="3" style={{ resize: 'none'}} placeholder="Menssagem" />

                <Link className="btn btn-outline-primary text-uppercase mt-1">
                  <i className="fab fa-telegram-plane" />
                  &nbsp;Enviar
                </Link>
              </form>
            </div>
          </div>
        </div>
      </section>
    )
  }
}

export default Contato;